<template>
    <div class="coupon-list">
        <div class="table-box">
            <form novalidate @submit.prevent="search">
                <div class="search-box">
                    <table class="table-search" cellspacing="0" cellpadding="0" border="0">
                        <tbody>
                        <tr>
                            <th style="border-radius:5px;height:70px;">검색 기간</th>
                            <td>
                                <div style="display: flex;max-height: 40px;padding-top:6px;">
                                    <div class="date-box">
                                        <v-btn depressed :class="'white-button '+dayClass.to" @click="setDay('to')">오늘
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.yer" @click="setDay('yer')">
                                            어제
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.thr" @click="setDay('thr')">
                                            3일
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.week" @click="setDay('week')">
                                            1주일
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.mon" @click="setDay('mon')">
                                            1개월
                                        </v-btn>
                                        <v-btn depressed :class="'white-button '+dayClass.tmon" @click="setDay('tmon')">
                                            3개월
                                        </v-btn>
                                    </div>
                                    <div style="display: flex;margin-top:-11px;margin-left:10px;">
                                        <v-menu
                                                v-model="vmenu1"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                        >
                                            <template v-slot:activator="{ on, attrs }">
                                                <div class="input-box" style="width:120px;">
                                                    <v-text-field
                                                            v-model="stdate"
                                                            readonly
                                                            v-bind="attrs"
                                                            v-on="on"
                                                    ></v-text-field>
                                                </div>
                                            </template>
                                            <v-date-picker
                                                    v-model="stdate"
                                                    @input="vmenu1 = false"
                                            ></v-date-picker>
                                        </v-menu>

                                        <div style="width:30px;text-align: center;line-height: 50px;height:50px;">~
                                        </div>

                                        <v-menu
                                                v-model="vmenu2"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                        >
                                            <template v-slot:activator="{ on, attrs }">
                                                <div class="input-box" style="width:120px;">
                                                    <v-text-field
                                                            v-model="endate"
                                                            readonly
                                                            v-bind="attrs"
                                                            v-on="on"
                                                    ></v-text-field>
                                                </div>
                                            </template>
                                            <v-date-picker
                                                    v-model="endate"
                                                    @input="vmenu2 = false"
                                            ></v-date-picker>
                                        </v-menu>
                                    </div>
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
                <div class="button-row">
                    <v-btn class="white-button" depressed @click="searchReset()"><span>초기화</span></v-btn>
                    <v-btn class="search-button" type="submit" depressed><span>검색</span></v-btn>
                </div>
            </form>
            <div class="table-container">
                <table class="table-list" cellpadding="0" cellspacing="0" border="0" style="width:100%;">
                    <thead>
                    <tr>
                        <th>번호</th>
                        <th style="width:60%">검색어</th>
                        <th>검색횟수</th>
                        <th>비율</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(item,index) in dataList">
                        <td>{{index+1}}</td>
                        <td>{{item.name}}</td>
                        <td>{{util.numberFormat(item.hits)}}</td>
                        <td>{{item.per}}%</td>
                    </tr>
                    </tbody>

                </table>
            </div>
        </div>
    </div>
</template>


<script>
    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_STATISTICS_KEYWORD,
    } from '@/config/admin/urls'
    import UTIL from '@/common/util'

    export default {
        data() {
            return {
                util: UTIL,
                stdate: '',
                endate: '',
                vmenu1: false,
                vmenu2: false,
                dayClass: {
                    to: '',
                    yer: '',
                    thr: '',
                    week: '',
                    mon: 'active',
                    tmon: '',
                },
                dataList: [],


            }
        },
        created() {
            this.setDay('mon');
            this.getData();
        },
        methods: {
            setDay(cmd) {
                for (let key in this.dayClass) {
                    if (key == cmd) {
                        this.dayClass[key] = 'active';
                    } else {
                        this.dayClass[key] = '';
                    }
                }
                const nowDate = new Date();
                let mon = nowDate.getMonth() + 1;
                let day = nowDate.getDate();
                const nowDay = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                this.endate = nowDay;
                switch (cmd) {
                    case 'to':
                        this.stdate = nowDay;
                        break;
                    case 'yer':
                        nowDate.setDate(day - 1);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'thr':
                        nowDate.setDate(day - 3);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'week':
                        nowDate.setDate(day - 7);
                        mon = nowDate.getMonth() + 1;
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'mon':
                        nowDate.setMonth(mon - 1);
                        mon = nowDate.getMonth();
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;
                    case 'tmon':
                        nowDate.setMonth(mon - 3);
                        mon = nowDate.getMonth();
                        day = nowDate.getDate();
                        this.stdate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                        break;

                }
            },
            searchReset() {
                this.stdate = '';
                this.endate = '';
                this.setDay('mon');

            },
            getData() {
                const frm = {
                    stdate: this.stdate.toString(),
                    endate: this.endate.toString(),
                }
                apiService.post(API_ADMIN_GET_STATISTICS_KEYWORD, frm, (res) => {
                    this.dataList = [];
                    let total = 0;
                    for (let item of res.keywordData) {
                        total+=parseInt(item.hits);
                    }
                    for (let item of res.keywordData) {
                        let per =(item.hits/total) * 100;
                        item.per = parseFloat(per.toString()).toFixed(1);
                        this.dataList.push(item);
                    }
                })
            },
            search() {
                this.getData();
            },

        }

    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/style/common.scss';
