            }
        }

    }

    .total {
        padding: 10px 30px 0 0;
        color: #000;
        font-weight: bold;
        font-size: 13px;
    }

    .button-row {
        text-align: right;
        padding-bottom: 20px;

        .white-button {
            border-radius: 0 0 0 5px;
